import React, { Component } from 'react';
import { Icon, Form, Button } from 'antd';
import { 
  Btn, 
  BtnSave, 
  BtnUpload,
  BtnAddNew,
} from 'components/common/button';

const ButtonGroup = Button.Group;
@Form.create({ name: 'formSerchIndividual' })
class MenuTabTop extends Component {
  state = {};

  render() {
    return (
      <ButtonGroup>
        <BtnSave
          onClick={() => this.props.savePlanSubject()}
        />
        <Btn
          onClick={() => this.props.autoPlanSubject()}
        >
          <Icon type="robot" />
          Xếp tự động
        </Btn>
        <Btn
          onClick={() => this.props.openDuplicatePlanSubject()}
        >
          <Icon type="snippets" theme="twoTone" twoToneColor="blue" />
          Sao chép TKB
        </Btn>
        <BtnUpload onClick={() => this.props.toggleImportPopup()} />
        <BtnAddNew title="Thêm PVĐK" onClick={() => this.props.addRegisterRange()} />
      </ButtonGroup>
    );
  }
}

export default MenuTabTop;



