import React, { Component } from 'react';
import { Form, Input } from 'antd';
import {
  TableDataEditCell,
  SelectAjax,
  SelectMultiAjax
} from 'components/common';
import { getStaffs } from 'services/staff';
import { getBuildingRoomForSelect } from 'services/buildingRoom';
import { getStudySessions } from 'services/studySession';
import {
  getDayOfWeek,
} from 'services/createSubject';
import { getStudyPeriod } from 'services/StudyPeriod';
import { getStudyPeriodTypes } from 'services/planSubjectDetail​';
// import { requiredRule } from 'utils/validateRules';
import moment from 'moment';

export default class TableClasses extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tkbData: {},
    };
  }

  formatAndSetDataTKB = (result) => {
    const tempDataTKB = {};
    result.data.items.forEach(item => {
      tempDataTKB[item.autoKey] = item;
    });
    this.setState({
      tkbData: { ...tempDataTKB }
    });
  }

  handleSaveCellData = (rowData) => {
    this.setState({
      tkbData: { ...this.state.tkbData, [rowData.autoKey]: rowData }
    });
    return rowData;
  };

  render() {
    const { service, paramSearch } = this.props;
    // const { getFieldValue } = this.props.form;


    const columns = [
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Tên môn',
        dataIndex: 'subjectLabel',
        key: 'subjectLabel',
      },
      {
        title: 'Số học trình',
        dataIndex: 'noOfCredit',
        key: 'noOfCredit',
        editable: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.noOfCredit`, {
              // initialValue: record.noOfCredit,
              // rules: [requiredRule],
            })(
              <Input
                onPressEnter={onSave}
                onBlur={onSave}
                refInput={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Lớp ghép',
        dataIndex: 'classSubjectConcat',
        key: 'classSubjectConcat',
        editable: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.classSubjectConcat`, {
              initialValue: record.classSubjectConcat,
              // rules: [requiredRule],
            })(
              <Input
                onPressEnter={onSave}
                onBlur={onSave}
                refInput={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Loại giờ',
        dataIndex: 'studyPeriodTypeId',
        key: 'studyPeriodTypeId',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.studyPeriodTypeId`, {
              initialValue: record.studyPeriodTypeId,
              // rules: [requiredRule],
            })(
              <SelectAjax
                keyValue="value"
                keyLabel="label"
                form={form}
                service={getStudyPeriodTypes}
                onPressEnter={onSave}
                onBlur={onSave}
                ref={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'S.tiết CTĐT',
        dataIndex: 'noOfPeriodProgram',
        key: 'noOfPeriodProgram',
        editable: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.noOfPeriodProgram`, {
              initialValue: record.noOfPeriodProgram,
              // rules: [requiredRule],
            })(
              <Input
                onPressEnter={onSave}
                onBlur={onSave}
                refInput={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'S.tiết/tuần',
        dataIndex: 'noOfPeriod',
        key: 'noOfPeriod',
        editable: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.periodInSession`, {
              initialValue: record.periodInSession,
              // rules: [requiredRule],
            })(
              <Input
                onPressEnter={onSave}
                onBlur={onSave}
                refInput={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Ca',
        dataIndex: 'studySessionId',
        key: 'studySessionId',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.studySessionId`, {
              initialValue: record.studySessionId,
              // rules: [requiredRule],
            })(
              <SelectAjax
                form={form}
                service={getStudySessions}
                onPressEnter={onSave}
                onBlur={onSave}
                ref={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Chia tiết',
        dataIndex: 'splitPeriod',
        key: 'splitPeriod',
        editable: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.splitPeriod`, {
              initialValue: record.splitPeriod,
              // rules: [requiredRule],
            })(
              <Input
                onPressEnter={onSave}
                onBlur={onSave}
                refInput={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Thứ',
        dataIndex: 'dayOfWeeks',
        key: 'dayOfWeeks',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.dayOfWeeks`, {
              initialValue: record.dayOfWeeks,
              // rules: [requiredRule],
            })(
              <SelectMultiAjax
                form={form}
                service={getDayOfWeek}
                allowGetObjSelected
                onPressEnter={onSave}
                onBlur={onSave}
                ref={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Từ tiết',
        dataIndex: 'fromStudyPeriodId',
        key: 'fromStudyPeriodId',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => {
          // console.log('re-render? studySessionId');
          const studySessionId = form.getFieldValue(`${record.autoKey}.fromStudyPeriodId`);
          return (
            <Form.Item>
              {form.getFieldDecorator(`${record.autoKey}.fromStudyPeriodId`, {
                initialValue: record.fromStudyPeriodId,
                // rules: [requiredRule],
              })(
                <SelectAjax
                  keyValue="id"
                  keyLabel="label"
                  form={form}
                  service={getStudyPeriod}
                  allowGetObjSelected
                  onPressEnter={onSave}
                  onBlur={onSave}
                  ref={setRefFormItem}
                  params={{ studySessionId }}
                  typeValue="int"
                />
              )}
            </Form.Item>
          )
        },
      },
      {
        title: 'Đến tiết',
        dataIndex: 'toStudyPeriodId',
        key: 'toStudyPeriodId',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => {
          const studySessionId = form.getFieldValue(`${record.autoKey}.studySessionId`);
          return (
            <Form.Item>
              {form.getFieldDecorator(`${record.autoKey}.toStudyPeriodId`, {
                initialValue: record.toStudyPeriodId,
                // rules: [requiredRule],
              })(
                <SelectAjax
                  keyValue="id"
                  keyLabel="label"
                  form={form}
                  service={getStudyPeriod}
                  allowGetObjSelected
                  onPressEnter={onSave}
                  onBlur={onSave}
                  ref={setRefFormItem}
                  params={{ studySessionId }}
                  typeValue="int"
                />
              )}
            </Form.Item>
          )
        },
      },
      {
        title: 'Từ tuần',
        dataIndex: 'fromWeek',
        key: 'fromWeek',
        editable: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.fromWeek`, {
              initialValue: record.fromWeek,
              // rules: [requiredRule],
            })(
              <Input
                onPressEnter={onSave}
                onBlur={onSave}
                refInput={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Đến tuần',
        dataIndex: 'toWeek',
        key: 'toWeek',
        editable: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.toWeek`, {
              initialValue: record.toWeek,
              // rules: [requiredRule],
            })(
              <Input
                onPressEnter={onSave}
                onBlur={onSave}
                refInput={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Từ ngày',
        dataIndex: 'fromDate',
        key: 'fromDate',
        render: (text, record) => {
          return (
            <span>{record.fromWeek ? moment().weeks(record.fromWeek).weekday(1).format('DD-MM-YYYY') : ""}</span>
          );
        },
      },
      {
        title: 'Đến ngày',
        dataIndex: 'toDate',
        key: 'toDate',
        render: (text, record) => {
          return (
            <span>{record.toWeek ? moment().weeks(record.toWeek).weekday(7).format('DD-MM-YYYY') : ""}</span>
          );
        },
      },
      {
        title: 'Giáo viên',
        key: 'teacherIds',
        dataIndex: 'teacherIds',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.teacherIds`, {
              initialValue: record.teacherIds,
              // rules: [requiredRule],
            })(
              <SelectMultiAjax
                keyValue="staffId"
                keyLabel="fullname"
                form={form}
                service={getStaffs}
                allowGetObjSelected
                onPressEnter={onSave}
                onBlur={onSave}
                ref={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Phòng',
        dataIndex: 'roomId',
        key: 'roomId',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.autoKey}.roomId`, {
              initialValue: record.roomId,
              // rules: [requiredRule],
            })(
              <SelectAjax
                keyValue="value"
                form={form}
                service={getBuildingRoomForSelect}
                allowGetObjSelected
                onPressEnter={onSave}
                onBlur={onSave}
                ref={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
    ];

    return (
      <TableDataEditCell
        paramSearch={paramSearch}
        service={async params => {
          const result = await service(params);
          this.formatAndSetDataTKB(result);
          return result;
        }}
        columns={columns}
        scroll={{ x: true }}
        handleSaveCellData={this.handleSaveCellData}
        allowGetData={
          !!paramSearch.trainTypeId &&
          !!paramSearch.facultyId &&
          !!paramSearch.courseId &&
          !!paramSearch.yearEduId &&
          !!paramSearch.semesterId
        }
        rowKey="autoKey"
      />
    );
  }
}



