import React, { Component } from 'react';
import { Form, Modal, Upload, Button, Icon, message, Row } from 'antd';
// import { isEmpty } from 'lodash';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import {
  createUpdatePlanSubjectDetailForTimetableFromFile
} from 'services/planSubjectDetail​';
import { browseClassNoGraduated } from 'services/schedule';

@Form.create({ name: 'SearchBar' })
class PopupImportFile extends Component {
  state = {
    // data: [],
    loading: false,
    // saving: false,
  };

  beforeUpload = async file => {
    this.setState({ loading: true });
    try {
      await this.props.form.validateFields(async (err, values) => {
        if (err) {
          return;
        }
        console.log(values);
        const formData = new FormData();
        formData.append('file', file);
        formData.append('name', file.name);
        formData.append('fileName', file.name);
        formData.append('yearEduId', values.yearEduId);
        formData.append('courseIds', values.courseIds);
        formData.append('trainTypeId', values.trainTypeId);
        formData.append('semesterId', values.semesterId);
        await createUpdatePlanSubjectDetailForTimetableFromFile(formData);
        message.success('Tải lên thành công');
      });

    } catch (error) {
      // console.log(JSON.stringify(error))
      // console.log(error.message)
      message.error(error)
    } finally {
      this.setState({ loading: false });
    }

  }

  handleCancel = () => {
    this.props.closePopup();
  }

  // handleSave = async () => {
  //   this.setState({ saving: true });
  //   try {
  //     if (!isEmpty(this.state.data)) {
  //       await saveMassStaffs(this.state.data)
  //     }
  //   } catch (err) {
  //     message.error("Có lỗi xãy ra");
  //   }
  //   this.setState({ saving: false });
  // }

  render() {
    const {
      loading,
      // saving 
    } = this.state;
    const { visible } = this.props;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      classId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Lớp',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };

    const componentFieldProps = {
      classId: {
        service: browseClassNoGraduated,
        keyLabel: 'classLabel',
        keyValue: 'classId',
      },
    };
    return (
      <Modal
        title="Nhập dữ liệu từ file Excel"
        visible={visible}
        width={900}
        onCancel={this.handleCancel}
        footer={[
          <Upload
            fileList={[]}
            beforeUpload={this.beforeUpload}
          >
            <Button loading={loading} >
              <Icon type="upload" /> Chọn file
            </Button>
          </Upload>
        ]}
      >
        <Form className="form-inline-label" layout="vertical">
          <Row gutter={24}>
            <FilterPathEducate
              form={this.props.form}
              optionFieldDecorator={optionFieldDecorator}
              componentFieldProps={componentFieldProps}
              column={6}
              courseIds
              studyField
              classes
              hideFacultyId
            />
            <YearSemesterInput
              form={this.props.form}
              optionFieldDecorator={optionFieldDecorator}
              span={5}
            />
          </Row>
        </Form>

      </Modal>
    );
  }
}

export default PopupImportFile;



