import { request } from 'utils';


const prefix = '/api/education';

export const getRegisterSessionLimit = ({
  trainTypeId,
  registerSessionId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/RegisterSessionLimit`,
    method: 'GET',
    params: {
      trainTypeId,
      registerSessionId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {},
  });

// {
//   "registerSessionId": 0,
//   "psdId": 0,
//   "trainTypeId": "string",
//   "courseId": "string",
//   "studyFieldId": "string",
//   "classIds": [
//     0
//   ]
// }
export const createRegisterSessionLimit = ({
  registerSessionId,
  psdId,
  trainTypeId,
  courseId,
  studyFieldId,
  classIds,
}) =>
  request({
    prefix,
    url: `/RegisterSessionLimit`,
    method: 'POST',
    params: {},
    data: {
      registerSessionId,
      psdId,
      trainTypeId,
      courseId,
      studyFieldId,
      classIds,
    },
  });

export const deleteRegisterSessionLimit = ({
  registerSessionId,
  psdId
}) =>
  request({
    prefix,
    url: `/RegisterSessionLimit`,
    method: 'DELETE',
    params: {
      registerSessionId,
      psdId
    },
    data: {},
  });

export const getDataForSelect = ({ trainTypeId, yearEduId, semesterId }) =>
  request({
    prefix,
    url: `/RegisterSession/DataForSelect`,
    params: {
      trainTypeId,
      yearEduId,
      semesterId,
      fullFormat: true,
    },
    method: 'GET',
  });


