import { request } from 'utils';

const prefix = '/api/categories';

export const getStudyPeriod = ({
  label,
  studySessionId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: `/StudyPeriod`,
    method: 'GET',
    params: {
      label,
      studySessionId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    }
  });



