import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import {
  Card,
  Row,
  Col,
  Spin,
  message
} from 'antd';

import {
  // createListScheduleWithDetail,
  autoPlanSubjectDetails
} from 'services/schedule';
import {
  browsePlanSubjectDetailForTimetable,
  createUpdatePlanSubjectDetailForTimetable,
  // createUpdatePlanSubjectDetailForTimetableFromFile
} from 'services/planSubjectDetail​';

import { getQueryParams } from 'utils/urlUtil';
import { CartHeadAction } from 'components/common';

import FormSearch from './FormSearch';
import TablePlanSubjectDetail from './TablePlanSubjectDetail';
import MenuTabTop from './MenuTabTop';
import ModalDuplicatePlan from './ModalDuplicatePlan';
import PopupImportFile from './PopupImportFile';
import ModalRegisterRange from './modalRegisterRange';

class ManualSchedulePage extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.refTableRaw = React.createRef();
    this.selectedCells = {};
    this.planDetailDataToSave = {};
    this.planDetailDataToDelete = [];
    this.state = {
      loading: false,
      openModalDup: false,
      paramSearch: {},
      importPopupVisible: false,
      showExportNoteModal: false,
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = async () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  savePlanSubject = async () => {
    const { paramSearch } = this.state;
    console.log(this.refTable);
    const formatData = {};
    formatData.planSubjectDetails = [];
    formatData.yearEduId = paramSearch.yearEduId;
    formatData.semesterId = paramSearch.semesterId;
    Object.keys(this.refTable.current.state.tkbData).forEach(key => {
      formatData.planSubjectDetails.push(this.refTable.current.state.tkbData[key]);
    })
    await createUpdatePlanSubjectDetailForTimetable(formatData);
    message.success('Lưu thành công');
  }

  autoPlanSubject = async () => {
    try {
      this.setState({
        loading: true,
      });
      const paramSearch = getQueryParams();
      if (paramSearch.classId && paramSearch.yearEduId && paramSearch.semesterId) {
        await autoPlanSubjectDetails(paramSearch);
        this.setState({
          loading: false,
        },
          () => {
            this.handleChangeQueryParams();
          });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  openDuplicatePlanSubject = async () => {
    this.setState({
      openModalDup: true,
    });
  }

  closeModal = () => {
    this.setState({
      openModalDup: false,
    });
  }

  toggleImportPopup = importPopupVisible => {
    console.log(importPopupVisible);
    this.setState({ importPopupVisible });
  };

  addRegisterRange = async () => {
    this.setState({
      showExportNoteModal: true,
    });
  }

  hideExportNoteModal = () => {
    this.setState({
      showExportNoteModal: false,
    });
  }


  render() {
    const {
      loading,
      openModalDup,
      importPopupVisible,
      showExportNoteModal
    } = this.state;
    return (
      <Spin spinning={loading}>
        <Card title="Xếp TKB thủ công">
          <Row gutter={24}>
            <Col span={24}>
              <FormSearch />
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <CartHeadAction
                style={{
                  textAlign: 'right',
                  margin: '0px -25px 10px -25px',
                  borderBottom: 0,
                }}
              >
                <MenuTabTop
                  savePlanSubject={this.savePlanSubject}
                  autoPlanSubject={this.autoPlanSubject}
                  openDuplicatePlanSubject={this.openDuplicatePlanSubject}
                  toggleImportPopup={() => this.toggleImportPopup(true)}
                  addRegisterRange={() => this.addRegisterRange()}
                />
              </CartHeadAction>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <TablePlanSubjectDetail
                service={browsePlanSubjectDetailForTimetable}
                paramSearch={this.state.paramSearch}
                ref={this.refTable}
                wrappedComponentRef={(form) => {
                  this.refTableRaw = form;
                }}
              />
            </Col>
          </Row>
        </Card>
        {openModalDup &&
          <ModalDuplicatePlan
            visible={openModalDup}
            closeModal={this.closeModal}
          />
        }
        <PopupImportFile
          visible={importPopupVisible}
          closePopup={() => this.toggleImportPopup(false)}
        />
        <ModalRegisterRange
          visible={showExportNoteModal}
          onSuccess={this.hideExportNoteModal}
          onCancel={this.hideExportNoteModal}
        />
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <ManualSchedulePage />
  </Page>
);



