import React, { Component, Fragment } from 'react';
import {
  Row,
  Col,
  Form,
  Modal,
  Spin,
  Table,
  Popconfirm,
  message
} from 'antd';
import { getTrainTypes } from 'services/trainType';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { getCourses } from 'services/course';
import { getStudyFields } from 'services/StudyField';
import { getClasss } from 'services/class';
import {
  getDataForSelect,
} from 'services/registerConfig';
import {
  getRegisterSessionLimit,
  deleteRegisterSessionLimit,
  createRegisterSessionLimit
} from 'services/registerSessionLimit';
import { BtnEdit, BtnDelete, BtnSave, BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax, SelectTable } from 'components/common';

@Form.create({ name: 'ModalCopyProgram' })
class ModalCopyProgram extends Component {
  state = {
    data: [],
    loading: false,
    currentEditRegisterRange: {},
    coursesData: [],
    studyFieldsData: [],
    classData: [],
    registerSessionId: "",
    trainTypeId: "",
  };

  onChangeCourse = (courseId) => {
    console.log('onChangeCourse');
    console.log(courseId);
    const { currentEditRegisterRange } = this.state;
    currentEditRegisterRange.courseId = courseId;
    this.setState({
      currentEditRegisterRange,
    });
  }

  onChangestudyField = (studyFieldId) => {
    console.log('onChangestudyField');
    console.log(studyFieldId);
    const { currentEditRegisterRange } = this.state;
    currentEditRegisterRange.studyFieldId = studyFieldId;
    this.setState({
      currentEditRegisterRange,
    });
  }

  onChangeClass = (classIds) => {
    console.log('onChangeClass');
    console.log(classIds);
    const { currentEditRegisterRange } = this.state;
    currentEditRegisterRange.classIds = classIds;
    this.setState({
      currentEditRegisterRange,
    });
  }

  getDataForSelects = async () => {
    const { trainTypeId } = this.state;
    // load data not depend
    await Promise.all([
      getCourses({}),
      getStudyFields({ trainTypeId }),
      getClasss({ trainTypeId }),
    ])
      .then((responses) => {
        this.setState({
          coursesData: [...responses[0].data.items],
          studyFieldsData: [...responses[1].data.items],
          classData: [...responses[2].data.items],
        });
      })
      .catch(() => {
        message.error('Có lỗi xãy ra');
      });
  }

  handleSearch = async () => {
    console.log("handleSearch");
    this.props.form.validateFields(async (err, values) => {
      try {
        this.setState({
          loading: true,
        });
        const res = await getRegisterSessionLimit({
          trainTypeId: values.trainTypeId,
          registerSessionId: values.registerSessionId,
        });
        this.setState({
          data: [...res.data.items],
          currentEditRegisterRange: {},
          loading: false,
          trainTypeId: values.trainTypeId,
          registerSessionId: values.registerSessionId,
        });
        this.getDataForSelects();
      } catch (error) {
        this.setState({
          loading: false,
        });
      }
    });
  }

  handleClickEdit = (psdId) => {
    console.log('edit');
    console.log(psdId);
    const { data } = this.state;
    const registerRange = data.filter(item => item.psdId === psdId);
    console.log(registerRange);
    const currentEditRegisterRangeTemp = {};
    currentEditRegisterRangeTemp.psdId = psdId;
    currentEditRegisterRangeTemp.courseId = registerRange[0].courseId;
    currentEditRegisterRangeTemp.studyFieldId = registerRange[0].studyFieldId;
    currentEditRegisterRangeTemp.classIds = registerRange[0].classIds;
    this.setState({
      currentEditRegisterRange: currentEditRegisterRangeTemp,
    });
  }

  handleClickDelete = async (registerSessionId, psdId) => {
    console.log('delete');
    console.log(registerSessionId);
    console.log(psdId);
    try {
      await deleteRegisterSessionLimit({ registerSessionId, psdId });
      message.success('Xoá thành công');
      this.handleSearch();
    } catch (error) {
      message.error('Đã có lỗi xảy ra.Vui lòng thử lại.');
    }
  }

  handleClickSave = async () => {
    const { currentEditRegisterRange, trainTypeId, registerSessionId } = this.state;
    console.log('save');
    console.log(currentEditRegisterRange);
    if (currentEditRegisterRange.courseId !== "" ||
      currentEditRegisterRange.studyFieldId !== "" ||
      currentEditRegisterRange.classIds.length > 0
    ) {
      try {
        this.setState({
          loading: true,
        });
        await createRegisterSessionLimit({
          trainTypeId,
          registerSessionId,
          psdId: currentEditRegisterRange.psdId,
          courseId: currentEditRegisterRange.courseId,
          studyFieldId: currentEditRegisterRange.studyFieldId,
          classIds: currentEditRegisterRange.classIds,
        });
        message.success('Lưu thành công');
        this.handleSearch();
      } catch (error) {
        this.setState({
          loading: false,
        });
      }
    } else {
      message.warn('Chọn ít nhất 1 Khoá - Ngành - Lớp');
    }

  }

  columnsPopup = [
    {
      title: 'Tên lớp TC',
      dataIndex: 'syllableCode',
      width: 200,
    },
    {
      title: 'Khoá',
      dataIndex: 'course',
      width: 200,
      render: (text, record) => {
        const { currentEditRegisterRange, coursesData } = this.state;
        return (
          currentEditRegisterRange.psdId === record.psdId ?
            (
              <SelectTable
                defaultValue={record.courseId}
                data={coursesData}
                onChange={(courseId) => {
                  this.onChangeCourse(courseId);
                }}
              />
            )
            :
            (
              <span>{record.course}</span>
            )
        );
      }
    },
    {
      title: 'Ngành',
      dataIndex: 'studyField',
      width: 200,
      render: (text, record) => {
        const { currentEditRegisterRange, studyFieldsData } = this.state;
        return (
          currentEditRegisterRange.psdId === record.psdId ?
            (
              <SelectTable
                defaultValue={record.studyFieldId}
                data={studyFieldsData}
                onChange={(studyFieldId) => {
                  this.onChangestudyField(studyFieldId);
                }}
              />
            )
            :
            (
              <span>{record.studyField}</span>
            )
        );
      }
    },
    {
      title: 'Lớp',
      dataIndex: 'classes',
      width: 200,
      render: (text, record) => {
        const { currentEditRegisterRange, classData } = this.state;
        return (
          currentEditRegisterRange.psdId === record.psdId ?
            (
              <SelectTable
                defaultValue={record.classIds}
                typeValue="int"
                data={classData}
                onChange={(classIds) => {
                  this.onChangeClass(classIds);
                }}
                mode="multiple"
              />
            )
            :
            (
              <span dangerouslySetInnerHTML={{ __html: record.classes }} />
            )
        );
      }
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      width: 100,
      render: (text, record) => {
        const { currentEditRegisterRange } = this.state;
        return (
          currentEditRegisterRange && currentEditRegisterRange.psdId ?
            (
              currentEditRegisterRange.psdId === record.psdId ?
                <BtnSave onClick={() => this.handleClickSave()} />
                :
                null
            )
            :
                <Fragment>
                  <BtnEdit onClick={() => this.handleClickEdit(record.psdId)} />
                  {(record.courseId !== "" || record.classIds.length !== 0 || record.studyFieldId !== "") ? (
                    <Popconfirm
                      title="Bạn có muốn xóa?"
                      okText="Xóa"
                      cancelText="Hủy"
                      onConfirm={() => this.handleClickDelete(record.registerSessionId, record.psdId)}
                    >
                      <BtnDelete />
                    </Popconfirm>
                  ) : null}
                </Fragment>
        )
      }
    },
  ];

  render() {
    const { loading, data } = this.state;
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    const trainTypeId = getFieldValue('trainTypeId');
    const layout = {
      labelCol: { span: 8 },
      wrapperCol: { span: 16 },
    };
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 18 },
      },
    };
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm!',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ!',
          },
        ],
      },
      registerSessionId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Đợt đăng ký!',
          },
        ],
      },
    };

    return (
      <Modal
        title='Phạm vi đăng ký'
        visible={this.props.visible}
        onCancel={this.props.onCancel}
        width={1000}
        footer={null}
      >
        <Spin spinning={loading}>
          <Form {...layout} onSubmit={this.handleSearch}>
            <Row gutter={24}>
              <Col xs={24} sm={12} md={12}>
                <Form.Item {...formItemLayout} label="Năm học">
                  {getFieldDecorator('yearEduId', optionFieldDecorator.yearEduId)(
                    <SelectAjax
                      service={getYearEducations}
                      setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                      typeValue="int"
                    />
                  )}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={12}>
                <Form.Item {...formItemLayout} label="Học kì">
                  {getFieldDecorator('semesterId', optionFieldDecorator.semesterId)(
                    <SelectAjax
                      // disabled={!!disabled}
                      setFieldsValue={setFieldsValue}
                      service={getSemesters}
                      params={{ yearEduId }}
                      allowGetData={!!yearEduId}
                      typeValue="int"
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={24}>
              <Col xs={24} sm={12} md={12}>
                <Form.Item {...formItemLayout} label="Hệ">
                  {getFieldDecorator('trainTypeId', optionFieldDecorator.trainTypeId)(
                    <SelectAjax service={getTrainTypes} />
                  )}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={12}>
                <Form.Item {...formItemLayout} label="Đợt đăng ký" required>
                  {getFieldDecorator('registerSessionId', optionFieldDecorator.registerSessionId)(
                    <SelectAjax
                      setFieldsValue={setFieldsValue}
                      service={getDataForSelect}
                      allowGetData={!!trainTypeId}
                      params={{ trainTypeId, yearEduId, semesterId }}
                      typeValue="int"
                      keyLabel="label"
                      keyValue="value"
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={24} style={{ textAlign: 'right' }}>
                <div className="btn-group">
                  <BtnSearch htmlType="submit" />
                  <BtnClear onClick={this.props.onCancel} />
                </div>
              </Col>
            </Row>
          </Form>
          <Table
            columns={this.columnsPopup}
            dataSource={data || []}
            bordered
            rowKey="psdId"
            pagination={false}
            scroll={{ y: 300, x: 900 }}
          />
        </Spin>
      </Modal>
    );
  }
}

export default ModalCopyProgram;



