import React, { Component } from 'react';
import { Col, Form, Row } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';

import { browseClassNoGraduated } from 'services/schedule';

@Form.create({ name: 'FormSearch' })
class FormSearch extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };
  render() {
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        // rules: [
        //   {
        //     required: true,
        //     message: 'Vui lòng chọn CN',
        //   },
        // ],
      },
      classId: {
        // rules: [
        //   {
        //     required: true,
        //     message: 'Vui lòng chọn Lớp',
        //   },
        // ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    const componentFieldProps = {
      classId: {
        service: browseClassNoGraduated,
        keyLabel: 'classLabel',
        keyValue: 'classId',
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate
            form={this.props.form}
            optionFieldDecorator={optionFieldDecorator}
            componentFieldProps={componentFieldProps}
          />
          <YearSemesterInput
            form={this.props.form}
            optionFieldDecorator={optionFieldDecorator}
            span={5}
          />
          <Col span={4}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearch;



